<template>
  <div>
    <div class="setting" @click="openSetting">
      <van-icon color="gray" size="20" name="setting-o" />
    </div>
    <div class="content">
      <p class="logo">
        <!-- <img src="@assets/images/crm.png" /> -->
      </p>
      <p class="crm">个人资料修改</p>
      <p>
        <input
          type="text"
          v-model="user_nickname"
          placeholder="用户昵称"
          class="account"
          value
        />
      </p>
      <p>
        <input
          type="password"
          v-model="old_pass"
          placeholder="旧密码"
          class="password"
          value
        />
      </p>
      <p>
        <input
          type="password"
          v-model="user_pass"
          placeholder="新密码"
          class="password"
          value
        />
      </p>
      <p>
        <input
          type="password"
          v-model="ruser_pass"
          placeholder="重复密码"
          class="password"
          value
        />
      </p>
      <div class="submit" @click="submitLogin">确认修改</div>
    </div>
    <!--setting-->
    <van-action-sheet v-model="show" :actions="actions" @select="onSelect" />
  </div>
</template> 
<script>
import { upPws, getUserInfo ,jobdayTable} from "@api/user";
import { Icon, ActionSheet, Cell, CellGroup } from "vant";
import localStorage from "@utils/store/localStorage";

import { trim } from "@utils";

export default {
  name: "ChangePassword",
  components: {
    [Icon.name]: Icon,

    [ActionSheet.name]: ActionSheet,
  },
  data: function () {
    return {
      checked: true,
      user_nickname: "",
      user_pass: "",
      old_pass: "",
      ruser_pass: "",
      show: false,
      actions: [{ name: "系统刷新" }, { name: "退出登录" }],
    };
  },
  mounted: function () {

     jobdayTable().then((res) => {});
    getUserInfo()
      .then(({ data }) => {
        this.user_nickname = data.user.user_nickname;
      })
      .catch((e) => {
        this.$dialog.message(e.msg);
      });
  },
  methods: {
    openSetting() {
      this.show = true;
    },
    onSelect(e) {
      if (e.name == "系统刷新") {
        this.show = false;
        localStorage.remove("ntoken");
        this.$dialog.message("清除成功");
      } else {
        this.show = false;
        localStorage.remove("ntoken");
        this.$router.replace({ path: "/" });
      }
    },
    submitLogin() {
      if (trim(this.user_nickname) == "") {
        this.$dialog.message("请输入昵称");
      } else if (trim(this.old_pass) == "") {
        this.$dialog.message("请输入旧密码");
      } else if (trim(this.user_pass) == "") {
        this.$dialog.message("请输入新密码");
      } else if (trim(this.user_pass) != trim(this.ruser_pass)) {
        this.$dialog.message("两次密码不一致");
      } else {
        upPws({
          user_nickname: this.user_nickname,
          old_pass: this.old_pass,
          user_pass: this.user_pass,
        })
          .then(({ data }) => {
            this.$dialog.message("修改成功");
            location.reload();
          })
          .catch((e) => {
            this.$dialog.message(e.msg);
          });
      }
    },
  },
};
</script>
